import React, { Component, Fragment } from 'react';
import { Badge } from 'reactstrap';

export default class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
        };
    }

    render() {
        return (
            <Fragment>
                <p className="mt-5 mb-5 text-center">
                    <img src={'./assets/logo-v1_2.png'} height="150px" />

                    <h1 align="center">json-obj-form-generator</h1>
                    <subtitle align="center">generate forms from JSON object</subtitle>

                    <div>
                        <a className="m-1" href="https://www.npmjs.com/package/json-obj-form-generator/v/1.2.8">
                            <img src="https://img.shields.io/badge/npm-v1.2.8-%23cb2821" alt="NPM" />
                        </a>
                        
                        <a className="m-1" href="https://github.com/radovan-pranda/json-obj-form-generator/blob/master/LICENSE">
                            <img src="https://img.shields.io/npm/l/json-obj-form-generator.svg" />
                        </a>
                    </div>
                </p>
                <section>
                    <h2>
                        Installation
                    </h2>
                    <note>
                        <code>
                            <npmcmd>npm install</npmcmd> --save json-obj-form-generator@1.2.8
                        </code>
                    </note>
                </section>

                <section>
                    <h2>
                        Styling
                    </h2>
                    <p>
                        Library json-obj-form-generator (a.k.a. JOFGEN) uses Bootstrap styles, which are not included, so
                        this needs to be installed as well. You can install Bootstrap CSS from NPM:
                    </p>
                    <note>
                        <code>
                            <npmcmd>npm install</npmcmd> --save bootstrap
                        </code>
                    </note>
                    <p>
                        Import styles in the <path>src/index.js</path> file:
                    </p>
                    <note>
                        <code>
                            <import>import</import> <path>'bootstrap/dist/css/bootstrap.min.css'</path>;<br />
                            <import>import</import> <path>'json-obj-form-generator/dist/css/designer.css'</path>; <comment># designer CSS styling - extends Bootstrap CSS</comment> <br />
                            <import>import</import> <path>'json-obj-form-generator/dist/css/generator.css'</path>; <comment># generator CSS styling - extends Bootstrap CSS</comment> <br />
                        </code>
                    </note>

                    <p>
                        There are some extra CSS files, that you can import:
                    </p>
                    <note>
                        <code>
                            <comment># patch to fix designer layout, if you are using Bootstrap v5</comment> <br />
                            <import>import</import> <path>'json-obj-form-generator/dist/css/designer-bootstrap-v5-patch.css'</path>; <br />
                            <br />
                            <comment># extra CSS styling for designer, which adds new card styles (fix patch included)</comment> <br />
                            <import>import</import> <path>'json-obj-form-generator/dist/css/designer-extra.css'</path>; 
                        </code>
                    </note>
                </section>     
            </Fragment>
        );

    }
}